import React, { PureComponent } from 'react'
import { Modal, message, Button } from 'antd'
import { API_CREATEPRODUCTIONORDERS_IMPORT } from 'api'
import { Import } from 'HOC'
import style from './style/Tool.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
const confirm = Modal.confirm
@connect((state) => ({ ...state.createProductionOrder }), mapDispatchToProps)
class TableTool extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
  }
  componentDidMount() {}
  componentWillUnmount() {
    const { update } = this.props
    update({ importResultList: [] })
  }
  /* 下载模版 */
  handleDownLoad = (key) => {
    const downloadData = JSON.parse(localStorage.getItem('b2bMmsDownload'))
    location.href = downloadData[key].downloadUrl
  }
  /* 导入成功 */
  handleImportSuccess = (res) => {
    const { update } = this.props
    const { result = [] } = res
    const { data = [] } = result
    update({
      tableLoading: false,
      modalTuttonLoading: false,
      importResultList: data
    })
    res && res.message === 'ok'
      ? message.success('导入成功')
      : res.message
      ? message.success(res.message)
      : ''
  }
  /* 报错导出 */
  handleExprotError = () => {
    const { update } = this.props
    update({
      tableLoading: false,
      modalTuttonLoading: false
    })
  }
  /* 报错导入 */
  handleImportError = () => {
    const { update } = this.props
    update({
      tableLoading: false,
      modalTuttonLoading: false
    })
  }
  // 删除选中项目
  handleDel = (data = []) => {
    const { update, selectedRowKeys, importResultList = [] } = this.props
    if (!(selectedRowKeys && selectedRowKeys.length > 0)) {
      return message.error('没有可以操作的选项')
    }
    let arrCopy =
      importResultList &&
      importResultList.length > 0 &&
      JSON.parse(JSON.stringify(importResultList))
    let result =
      arrCopy &&
      arrCopy.length > 0 &&
      arrCopy.filter((item, index, arr) => {
        let flag = true
        const { recordId = '' } = item
        selectedRowKeys &&
          selectedRowKeys.length > 0 &&
          selectedRowKeys.map((da, i, self) => {
            if (da == recordId) {
              flag = false
            }
          })
        return flag
      })
    update({
      importResultList: result,
      selectedRowKeys: []
    })
  }
  // 删除所有项目
  handleDelAll = () => {
    const { update, importResultList } = this.props
    if (!(importResultList && importResultList.length > 0)) {
      return message.error('没有可以操作的选项')
    }
    confirm({
      title: '温馨提示',
      content: '确认是否一键清空下发所有数据？',
      okText: '确认',
      width: 520,
      cancelText: '取消',
      onOk() {
        update({
          importResultList: [],
          selectedRowKeys: []
        })
      }
    })
  }
  handleOperator = () => {
    const { selectedRowKeys } = this.props
    if (!(selectedRowKeys && selectedRowKeys.length > 0)) {
      return message.error('没有可以操作的选项')
    }
    let self = this
    confirm({
      title: '温馨提示',
      content: '是否进行删除操作？',
      okText: '确认',
      width: 520,
      cancelText: '取消',
      onOk() {
        self.handleDel(selectedRowKeys)
      }
    })
  }
  render() {
    const {
      importResultList = [],
      modalTuttonLoading,
      menuPermissionIds = ''
    } = this.props
    return (
      <div className={`${style['table-tool']} ${style['table-tool-top0']}`}>
        {menuPermissionIds &&
          menuPermissionIds.includes('productOrder/create_import') && (
            <span className={style.btn_left_one}>
              <Import
                action={{ api: API_CREATEPRODUCTIONORDERS_IMPORT }}
                onOk={this.handleImportSuccess}
                onExprotError={this.handleExprotError}
                multiple={false}
                beforeImport={() => {
                  const { update } = this.props
                  update({
                    tableLoading: true,
                    modalTuttonLoading: true
                  })
                }}
                onResponse={(data) => {}}
              >
                <Button
                  type="primary"
                  icon="upload"
                  className={style.btn}
                  loading={modalTuttonLoading}
                >
                  导入
                </Button>
              </Import>
            </span>
          )}
        {menuPermissionIds &&
          menuPermissionIds.includes('productOrder/create_exoportTemplate') && (
            <Button
              type="primary"
              className={style.btn}
              icon="sync"
              onClick={() => {
                this.handleDownLoad('T01')
              }}
            >
              下载模板
            </Button>
          )}

        {menuPermissionIds &&
          menuPermissionIds.includes('productOrder/create_delete') && (
            <Button
              type="primary"
              className={style.btn}
              onClick={this.handleOperator}
            >
              删除
            </Button>
          )}

        {menuPermissionIds &&
          menuPermissionIds.includes('productOrder/create_clearAll') && (
            <Button
              type="primary"
              className={style.btn}
              disabled={importResultList && importResultList.length < 1}
              onClick={this.handleDelAll}
            >
              一键清空
            </Button>
          )}
        {menuPermissionIds &&
          menuPermissionIds.includes('productOrder/create_submit') && (
            <Button
              type="primary"
              className={style.btn_right}
              htmlType="submit"
              loading={modalTuttonLoading}
              disabled={importResultList && importResultList.length < 1}
            >
              提交
            </Button>
          )}
      </div>
    )
  }
}
export default TableTool



