import * as actionType from './actionType'
import { getData } from 'util'
import {
  API_CREATEPRODUCTIONORDERS_CREATE,
  API_CREATEPRODUCTIONORDERS_UPDATE
} from 'api'
//同步
export const update = object => ({
  type: actionType.CREATEPRODUCTIONORDER_UPDATE,
  data: object
})
// 创建/编辑生产订单
export const handleAddOrEdit = (reqData, callBack) => dispatch => {
  dispatch({
    type: actionType.CREATEPRODUCTIONORDER_UPDATE,
    data: { modalTuttonLoading: true }
  })
  let api =
    reqData.data && reqData.data.id
      ? API_CREATEPRODUCTIONORDERS_UPDATE
      : API_CREATEPRODUCTIONORDERS_CREATE
  getData({ api, ...reqData })
    .then(data => {
      // 相应的逻辑判断
      dispatch({
        type: actionType.CREATEPRODUCTIONORDER_UPDATE,
        data: {
          modalTuttonLoading: false,
          isShowModal: false
        }
      })
      callBack && callBack()
    })
    .catch(err => {
      dispatch({
        type: actionType.CREATEPRODUCTIONORDER_FAIL
      })
    })
}



